export const removeItem = createAsyncThunk(
    "item/remove", 
    async(id, thunkAPI) => {
        try {
            await api.removeItemApi(id);
            return id;
        } catch ({response}) {
            console.log(response);
            const {status, statusText: message } = response;
            return thunkAPI.rejectWithValue({status, message});
        }
    }
);